.input {
    margin: 2rem auto;
    padding: 2rem;
    width: 90%;
    max-width: 30rem;
  }
  
  .input label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.7rem;
  }
  
  .input input {
    font: inherit;
    display: block;
    width: 100%;
    border: 1px solid #ccc;
    padding: 0.1rem;
    margin-bottom: 0.7rem;
    background-color:rgb(208, 218, 198);
  }
  
  .input input:focus {
    outline: none;
    border-color: #074ea0;
  }